.item {
  background-color: #fff;
  float: left;
  padding: 5px;
  margin: 5px;

  img {
      display: block;
      margin: auto;
      height: 200px;
      width: 200px;
  }

  p {
    margin: 0;
  }
}

.item.panel-primary {
  .panel-heading {
    h1,h2,h3,h4,h5,h6 {
      color: white !important;
    }
  }
}